import { request } from '../../request.js'
export default {
	data() {
		return {
			navList: {
				currentIndex: 0,
				list: [
					{
						name: '全部',
						des: '大促销 好礼不断'
					},
					{
						name: '限量清仓',
						des: '低至1折'
					},
					{
						name: '超值精选',
						des: '低至2折'
					},
					{
						name: '人气热卖',
						des: '低至3折'
					},
					{
						name: '9.9包邮',
						des: '低至9.9元'
					},
					{
						name: '19.9任选',
						des: '低至19.9元'
					},
				]
			},
			// 储存可视区域的高度
			statusHeight: '',
			// 储存折扣类型
			type: '',
			// 储存折扣度
			dynamics: '',
			// 储存数据
			dataList: [],
			// 储存接口返回的数据
			total_count: '',
			// 请求的页数(默认为1)
			page: 1,
			flag: true,
			$src: this.$src
		}
	},
	onLoad(options) {
		// 定位当前处于哪个栏目列表中
		this.navList.currentIndex = this.$store.state.discount;
		if(!options.type) {
			for(let key in options) {
				this.type = key;
				this.dynamics = options[key];
				this.getGoodsList(this.type,this.dynamics,this.page)
			}
		}
		else {
			this.getGoodsList(undefined,undefined,this.page)
		}
		// 读取屏幕可视高度
		uni.getSystemInfo({
			success: res => {
				this.statusHeight = res.windowHeight + 'px'
			}
		})
		// 默认读取全部
		// this.changeNav(0);
	},
	onReachBottom() {
		this.loadMore()
	},
	methods: {
		changeNav(index) {
			this.navList.currentIndex = index;
			switch(index) {
				case 0:
				this.dataList = [];
				this.getGoodsList(undefined,undefined,this.page);
				break;
				case 1: 
				this.dataList = [];
				this.getGoodsList('discount',10,this.page);
				break;
				case 2:
				this.dataList = [];
				this.getGoodsList('discount',20,this.page);
				break;
				case 3:
				this.dataList = [];
				this.getGoodsList('discount',30,this.page);
				break;
				case 4:
				this.dataList = [];
				this.getGoodsList('price',9.9,this.page);
				break;
				case 5:
				this.dataList = [];
				this.getGoodsList('price',19.9,this.page);
				break;
			}
		},
		getGoodsList(type='',dynamics='',page) {
			uni.showToast({
				title: '数据加载中',
				icon: 'loading'
			});
			request({
				url: 'index.php?s=/wap/goods/discountGoods',
				method: 'post',
				data: {
					[type]: dynamics,
					page: page
				}
			}).then(res => {
				uni.hideToast();
				// 接口数据返回条数
				this.total_count = res.data.total_count;
				// 处理接口返回的图片
				let data = res.data.data;
				for(let i = 0;i < data.length;i++) {
					data[i].pic_cover_small = this.$api + data[i].pic_cover_small
				}
				this.dataList = [...this.dataList,...data];
			})
		},
		// 上拉加载更多
		loadMore() {
			if(this.flag) {
				this.page = this.page + 1;
				this.getGoodsList(this.type,this.dynamics,this.page);
			}
			if(this.dataList.length >= this.total_count) {
				this.flag = false
			}
		},
		// 跳转到商品详情页
		handleDetail(id) {
			uni.navigateTo({
				url: '/pages/common/goods-detail/goods-detail?id='+id
			})
		}
	}
}
